import React, { useEffect, useState } from "react";
import styles from "./productDetail.module.css";
import { Modal } from "react-responsive-modal";
import { useCookies } from "react-cookie";
import "react-responsive-modal/styles.css";
import "react-toastify/dist/ReactToastify.css";
if (window.meta.product) {
    require("./removeCss.css");
}

const ProductContainer = (props) => {
    const [generalSettings, setGeneralSettings] = useState();
    const [cookies, setCookie, removeCookie] = useCookies("LM_Quote_sessionId");
    const [openSecond, setOpenSecond] = useState(false);
    console.log(props);
    const onOpenSecondModal = (e) => {
        setOpenSecond(true);
    };


    useEffect(() => {
        if (window.meta.product) {
            const getButtonSettings = async () => {
                const res = await fetch(
                    `${process.env.REACT_APP_API_URL}` +
                        "getButtonSettings/" +
                        window.location.host,
                    {
                        method: "GET",
                    }
                );
                const settings_data = await res.json();
                setGeneralSettings(settings_data);
            };
            getButtonSettings();
        }
    }, []);
    let handleAddToQuote = async (e) => {
        console.log("hi");
        const requestOptions = {
            method: "POST",
            headers: { "Content-Type": "application/json" },
            body: JSON.stringify({
                shop_domain: window.location.host,
                session_id: cookies.LM_Quote_sessionId,
                product_id: window.meta.product.id,
            }),
        };
        try {
            const res = await fetch(
                `${process.env.REACT_APP_API_URL}` + "addToQuote",
                requestOptions
            );
            const quote_data = await res.json();
            const res1 = await fetch(
                `${process.env.REACT_APP_API_URL}` +
                    "getQuotedProductCount",
                    requestOptions
            );
            const set_count = await res1.json();
            props.callback(set_count.data);
            // window.location.reload();
        } catch (error) {
            console.log();
        }
    };
    let handleAddToQuoteToast = async (e) => {
        const requestOptions = {
            method: "POST",
            headers: { "Content-Type": "application/json" },
            body: JSON.stringify({
                shop_domain: window.location.host,
                session_id: cookies.LM_Quote_sessionId,
                product_id: window.meta.product.id,
            }),
        };
        try {
            const res = await fetch(
                `${process.env.REACT_APP_API_URL}` + "addToQuote",
                requestOptions
            );
            const quote_data = await res.json();
            const res1 = await fetch(
                `${process.env.REACT_APP_API_URL}` +
                    "getQuotedProductCount" ,
                    requestOptions
            );
            const set_count = await res1.json();
            props.callback(set_count.data);
            props.toastCallback(
                `${window.LMSettingsScript.translation_settings.toast}`
            );
        } catch (error) {
            console.log();
        }
    };
    if (window.LMSettingsScript.general_settings.msg_type === "1") {
        return (
            <div>
                <style>
                    {`
                        .lm_add_to_quote_button{
                            background: ${window.LMSettingsScript.general_settings.require_btn_bg};
                            margin-bottom: 12px;
                        }
                        .lm_add_to_quote_button span {
                            color: ${window.LMSettingsScript.general_settings.require_btn_txt};
                        }
                `}
                </style>
                {/* <div className={styles.lm_add_to_quote_button}> */}
                <button
                    type="button"
                    // title="Submit"
                    className="btn button button--full-width lm_add_to_quote_button"
                    onClick={() => {
                        onOpenSecondModal();
                        handleAddToQuote();
                    }}
                >
                    <span>
                        {
                            window.LMSettingsScript.translation_settings
                                .req_qyt_txt
                        }
                    </span>
                </button>
                {/* <a
                        href="#!"
                        onClick={() => {
                            onOpenSecondModal();
                            handleAddToQuote();
                        }}
                    >
                        {
                            window.LMSettingsScript.translation_settings
                                .req_qyt_txt
                        }
                    </a> */}
                <Modal
                    open={openSecond}
                    onClose={() => setOpenSecond(false)}
                    center
                    classNames={{
                        overlay: "popup_Overlay",
                        modal: "popup-form",
                    }}
                >
                    <div className="Diamond-form">
                        <div className="requested-form">
                            <p>
                                The product{" "}
                                <strong>
                                    {window.meta.product.variants[0].name}
                                </strong>{" "}
                                is added to your quote.
                            </p>
                        </div>
                        <form>
                            <div>
                                <div className="prefrence-action">
                                    <div className="prefrence-action action moveUp">
                                        <button
                                            // type="submit"
                                            title="Submit"
                                            className="btn button preference-btn"
                                            onClick={() => setOpenSecond(false)}
                                        >
                                            <span>
                                                {
                                                    window.LMSettingsScript
                                                        .translation_settings
                                                        .cont_shop_btn_txt
                                                }
                                            </span>
                                        </button>
                                        <button
                                            // type="submit"
                                            onClick={(e) => {
                                                e.preventDefault();
                                                window.location.href =
                                                    "https://" +
                                                    window.location.host +
                                                    "/pages/lm-request-for-quote";
                                            }}
                                            title="Submit"
                                            className="btn button preference-btn"
                                        >
                                            <span>View Quote</span>
                                        </button>
                                    </div>
                                </div>
                            </div>
                        </form>
                    </div>
                </Modal>
                {/* </div> */}
            </div>
        );
    } else if (window.LMSettingsScript.general_settings.msg_type === "2") {
        return (
            <div>
                <style>
                    {`
                        .lm_add_to_quote_button{
                            background: ${window.LMSettingsScript.general_settings.require_btn_bg};
                            margin-bottom: 12px;
                        }
                        .lm_add_to_quote_button span {
                            color: ${window.LMSettingsScript.general_settings.require_btn_txt};
                        }
                `}
                </style>
                {/* <div className={styles.lm_add_to_quote_button}> */}
                <button
                    type="button"
                    // title="Submit"
                    className="btn button button--full-width lm_add_to_quote_button"
                    onClick={(e) => {
                        handleAddToQuote();
                        e.preventDefault();
                        window.location.href =
                            "https://" +
                            window.location.host +
                            "/pages/lm-request-for-quote";
                    }}
                >
                    <span>
                        {
                            window.LMSettingsScript.translation_settings
                                .req_qyt_txt
                        }
                    </span>
                </button>
                {/* <a
                        href="#!"
                        onClick={(e) => {
                            handleAddToQuote();
                            e.preventDefault();
                            window.location.href =
                                "https://" +
                                window.location.host +
                                "/pages/lm-request-for-quote";
                        }}
                    >
                        {
                            window.LMSettingsScript.translation_settings
                                .req_qyt_txt
                        }
                    </a> */}
                {/* </div> */}
            </div>
        );
    } else {
        return (
            <div>
                <style>
                    {`
                        .lm_add_to_quote_button{
                            background: ${window.LMSettingsScript.general_settings.require_btn_bg};
                            margin-bottom: 12px;
                        }
                        .lm_add_to_quote_button span {
                            color: ${window.LMSettingsScript.general_settings.require_btn_txt};
                        }
                `}
                </style>
                {/* <div className={styles.lm_add_to_quote_button}> */}
                <button
                    type="button"
                    // title="Submit"
                    className="btn button button--full-width lm_add_to_quote_button"
                    onClick={() => {
                        handleAddToQuoteToast();
                    }}
                >
                    <span>
                        {
                            window.LMSettingsScript.translation_settings
                                .req_qyt_txt
                        }
                    </span>
                </button>
                {/* <a
                        href="#!"
                        onClick={() => {
                            handleAddToQuoteToast();
                        }}
                    >
                        {
                            window.LMSettingsScript.translation_settings
                                .req_qyt_txt
                        }
                    </a> */}
                {/* </div> */}
            </div>
        );
    }
};

export default ProductContainer;
