import React, { useEffect, useState } from "react";
import ReactDOM from "react-dom";
import { CONST } from "./constants";
import { config } from "../config";
import ProductContainer from "../components/ProductDetail/ProductContainer";
import QuoteButton from "../components/QuoteButton/QuoteButton";
import LMRequestForQuotePage from "../components/RequestForQuotePage/LMRequestForQuotePage";
import ProductListPage from "../components/ProductListPage/ProductListPage";
import LoginPrice from "../components/LoginPrice/LoginPrice";
import { useCookies } from "react-cookie";
import "./custom.css";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";

const {
    ADD_TO_QUOTE_BTN,
    HIDE_PRICE,
    QUOTE_BUTTON,
    REQUEST_A_QUOTE_PAGE,
    BUY_NOW,
    ADD_TO_CART,
} = CONST;

const toastList = new Set();

function handleToast(e) {
    const id = toast.loading(e, {
        position: "top-center",
        autoClose: 3000,
    });
    setTimeout(() => {
        toast.update(id, {
            render: e,
            type: toast.TYPE.INFO,
            autoClose: 3000,
        });
    }, 1000);
    setTimeout(() => {
        toast.dismiss(id);
    }, 3000);
    toastList.add(id);
}

const App = () => {
    const [count, setCount] = useState();
    const [getHandle, setHandle] = useState([]);
    const [getHandleForAllProducts, setHandleForAllProducts] = useState([]);
    const [cookies, setCookie, removeCookie] = useCookies("LM_Quote_sessionId");
    const [getHandleDetailPage, setHandleDetailPage] = useState([]);
    const [
        getHandleForAllProductsDetailPage,
        setHandleForAllProductsDetailPage,
    ] = useState([]);

    const [enableProdDetails, setEnableProdDetails] = useState();
    const add_to_qoute_btn = document.querySelector(
        "." + config[ADD_TO_QUOTE_BTN].className
    );
    const request_for_quote_page = document.querySelector(
        "." + config[REQUEST_A_QUOTE_PAGE].className
    );
    const qyt_btn = document.querySelector(
        "." + config[QUOTE_BUTTON].className
    );
    const hide_price = document.querySelector(
        "." + config[HIDE_PRICE].className
    );
    const buy_now = document.querySelector(config[BUY_NOW].target);
    if (
        window.LMSettingsScript.general_settings.hide_buy_btn === "1" &&
        window.meta.page.pageType === "product"
    ) {
        buy_now.style.display = "none";
    }

    const add_to_cart = document.querySelector(config[ADD_TO_CART].target);
    if (
        window.LMSettingsScript.general_settings.hide_add_cart === "1" &&
        add_to_cart
    ) {
        add_to_cart.style.display = "none";
    }
    const handleCount = (e) => {
        setCount(e);
    };
    const handleIncDecCount = (e) => {
        setCount(e);
    };

    const setOnLoadCookie = () => {
        if (!cookies.LM_Quote_sessionId) {
            var randomstring = require("randomstring");
            let cookieKey = randomstring.generate(); //size 49 by default
            setCookie("LM_Quote_sessionId", cookieKey, { path: "/" });
        }
    };

    //ADDING HIDE CLASS TO COLLECTION PAGE
    function setClassToButton() {
        var collection_href = 'a.full-unstyled-link[href*="/products/"]';
        var product_item = ".card > .card__content .card__information";
        document.querySelectorAll(collection_href).forEach((t) => {
            var n = t
                .getAttribute("href")
                .substring(t.getAttribute("href").lastIndexOf("/"))
                .replace("/", "")
                .split("?")[0];
            if (-1 == n.indexOf(".")) {
                var addToQuoteElement = document.createElement("div");
                var hidePriceElement = document.createElement("div");
                if (null != t.closest(product_item)) {
                    var a = t.closest(product_item);
                    if (window.product_type === 2) {
                        if (
                            window.LMSettingsScript.general_settings
                                .btn_prod_list === "1"
                        ) {
                            setHandleForAllProducts((current) => [
                                ...current,
                                n,
                            ]);
                            a.parentNode.insertBefore(
                                addToQuoteElement,
                                a.nextSibling
                            );
                            addToQuoteElement.setAttribute("id", n);
                            addToQuoteElement.classList.add(
                                "lm_custom_collection_class_q78er"
                            );
                        }
                    }
                    //CHECKING IF HIDE PRICE TO ALL PRODUCTS
                    if (
                        window.LMSettingsScript.hide_price_settings
                            .hide_price === "2"
                    ) {
                        if (
                            window.LMSettingsScript.hide_price_settings
                                .all_products_req_login === "1"
                        ) {
                            a.parentNode.insertBefore(
                                hidePriceElement,
                                a.nextSibling
                            );
                            hidePriceElement.classList.add(
                                "lm_hide_price_login_class_q78er"
                            );
                        }
                        if (window.meta.page.customerId === undefined) {
                            a.querySelector(
                                config[HIDE_PRICE].target
                            ).style.display = "none";
                        }
                    }
                    if (window.product_type === 1) {
                        window.LMSelectedProductHandles?.map((collect) => {
                            if (collect === n) {
                                //CHECKING IF HIDE PRICE IS TO ONLY QUOTED PRODUCTS
                                if (
                                    window.LMSettingsScript.hide_price_settings
                                        .hide_price === "3"
                                ) {
                                    if (
                                        window.LMSettingsScript
                                            .hide_price_settings
                                            .quote_products_req_login === "1"
                                    ) {
                                        a.parentNode.insertBefore(
                                            hidePriceElement,
                                            a.nextSibling
                                        );
                                        hidePriceElement.classList.add(
                                            "lm_hide_price_login_class_q78er"
                                        );
                                    }

                                    if (
                                        window.meta.page.customerId ===
                                        undefined
                                    ) {
                                        a.querySelector(
                                            config[HIDE_PRICE].target
                                        ).style.display = "none";
                                    }
                                }

                                if (
                                    window.LMSettingsScript.general_settings
                                        .btn_prod_list === "1"
                                ) {
                                    setHandle((current) => [
                                        ...current,
                                        collect,
                                    ]);
                                    a.parentNode.insertBefore(
                                        addToQuoteElement,
                                        a.nextSibling
                                    );
                                    addToQuoteElement.setAttribute("id", n);
                                    addToQuoteElement.classList.add(
                                        "lm_custom_collection_class_q78er"
                                    );
                                }
                            } else {
                                a.parentNode.insertBefore(
                                    addToQuoteElement,
                                    a.nextSibling
                                );
                                addToQuoteElement.setAttribute("id", n);
                                addToQuoteElement.classList.add(
                                    "lm_custom_collection_class_q78er"
                                );
                            }
                        });
                    }
                }
            }
        });
    }
    //END
    if (window.meta.page.pageType === "product") {
        document.querySelectorAll(config[HIDE_PRICE].target).forEach((t) => {
            var hidePriceElement = document.createElement("div");
            //CHECKING IF HIDE PRICE IS TO ONLY QUOTED PRODUCTS
            if (
                window.LMSettingsScript.hide_price_settings.hide_price === "3"
            ) {
                window.LMSelectedProductIds.map((ids) => {
                    if (window.meta.product.gid === ids) {
                        if (
                            window.LMSettingsScript.hide_price_settings
                                .quote_products_req_login === "1"
                        ) {
                            t.parentNode.insertBefore(
                                hidePriceElement,
                                t.nextSibling
                            );
                            hidePriceElement.classList.add(
                                "lm_hide_price_login_class_q78er"
                            );
                        }
                        if (window.meta.page.customerId === undefined) {
                            t.style.display = "none";
                        }
                    }
                });
            }
            //CHECKING IF HIDE PRICE IS TO ALL PRODUCTS
            if (
                window.LMSettingsScript.hide_price_settings.hide_price === "2"
            ) {
                if (
                    window.LMSettingsScript.hide_price_settings
                        .all_products_req_login === "1"
                ) {
                    t.parentNode.insertBefore(hidePriceElement, t.nextSibling);
                    hidePriceElement.classList.add(
                        "lm_hide_price_login_class_q78er"
                    );
                }

                if (window.meta.page.customerId === undefined) {
                    t.style.display = "none";
                }
            }
        });
    }
    useEffect(() => {
        setOnLoadCookie();
        setClassToButton();
    }, []);
    return (
        <>
            <ToastContainer />
            {/* CREATE CONTAINER FOR STICKY QUOTE BUTTON */}
            {qyt_btn &&
                ReactDOM.createPortal(<QuoteButton data={count} />, qyt_btn)}
            {/* CREATE CONTAINER FOR PRODUCT DETAIL PAGE IF SELECTED ALL PRODUCTS*/}
            {add_to_qoute_btn &&
            window.LMSettingsScript.general_settings.prod_details === "1" &&
            window.product_type === 2 &&
            window.meta.product.gid
                ? ReactDOM.createPortal(
                      <ProductContainer
                          callback={handleCount}
                          toastCallback={handleToast}
                      />,
                      add_to_qoute_btn
                  )
                : null}
            {/* CREATE CONTAINER FOR PRODUCT DETAIL PAGE  IF SELECTED MANUAL PRODUCTS*/}
            {add_to_qoute_btn &&
                window.LMSettingsScript.general_settings.prod_details === "1" &&
                window.product_type === 1 &&
                window.meta.page.pageType === "product" &&
                window.LMSelectedProductIds.map((ids) => {
                    if (window.meta.product.gid === ids) {
                        return ReactDOM.createPortal(
                            <ProductContainer
                                callback={handleCount}
                                toastCallback={handleToast}
                            />,
                            add_to_qoute_btn
                        );
                    }
                })}
            {/* CREATE CONTAINER FOR LM REQUEST A QUOTE PAGE */}
            {request_for_quote_page &&
            window.location.pathname === "/pages/lm-request-for-quote"
                ? ReactDOM.createPortal(
                      <LMRequestForQuotePage
                          callback={handleCount}
                          incDecCallback={handleIncDecCount}
                      />,
                      request_for_quote_page
                  )
                : null}
            {/* CREATE CONTAINER FOR PRODUCT LIST / COLLECTION PAGES  QUOTED PRODUCTS*/}
            {window.product_type === 1 &&
                getHandle.map((data) => {
                    return ReactDOM.createPortal(
                        <ProductListPage
                            props={data}
                            callback={handleCount}
                            toastCallback={handleToast}
                        />,
                        document.getElementById(data)
                    );
                })}
            {/* CREATE CONTAINER FOR PRODUCT LIST / COLLECTION PAGES ALL PRODUCTS*/}
            {window.product_type === 2 &&
                getHandleForAllProducts.map((data) => {
                    return ReactDOM.createPortal(
                        <ProductListPage
                            props={data}
                            callback={handleCount}
                            toastCallback={handleToast}
                        />,
                        document.getElementById(data)
                    );
                })}

            {/* LOGIN PRICE CREATE PORTAL IF SELECTED FOR QUOTED PRODUCTS */}
            {window.LMSettingsScript.hide_price_settings.hide_price === "3" &&
                window.LMSettingsScript.hide_price_settings
                    .quote_products_req_login === "1" &&
                window.meta.page.customerId === undefined &&
                getHandle.map((data) => {
                    return ReactDOM.createPortal(
                        <LoginPrice />,
                        document.getElementById(data)
                    );
                })}
            {/* LOGIN PRICE CREATE PORTAL IF SELECTED FOR ALL PRODUCTS */}
            {window.LMSettingsScript.hide_price_settings.hide_price === "2" &&
                window.LMSettingsScript.hide_price_settings
                    .all_products_req_login === "1" &&
                window.product_type === "2" &&
                window.meta.page.customerId === undefined &&
                getHandleForAllProducts.map((data) => {
                    console.log(data);
                    return ReactDOM.createPortal(
                        <LoginPrice />,
                        document.getElementById(data)
                    );
                })}
            {/* LOGIN PRICE CREATE PORTAL IF SELECTED FOR QUOTED PRODUCTS DETAIL PAGE */}
            {hide_price &&
                window.LMSettingsScript.hide_price_settings.hide_price ===
                    "3" &&
                window.LMSettingsScript.hide_price_settings
                    .quote_products_req_login === "1" &&
                window.meta.page.customerId === undefined &&
                window.LMSelectedProductIds.map((ids) => {
                    if (window.meta.product.gid === ids) {
                        return ReactDOM.createPortal(
                            <LoginPrice />,
                            hide_price
                        );
                    }
                })}
            {/* LOGIN PRICE CREATE PORTAL IF SELECTED FOR ALL PRODUCTS DETAIL PAGE*/}
            {hide_price &&
            window.LMSettingsScript.hide_price_settings.hide_price === "2" &&
            window.meta.page.customerId === undefined &&
            window.LMSettingsScript.hide_price_settings
                .all_products_req_login === "1"
                ? ReactDOM.createPortal(<LoginPrice />, hide_price)
                : null}
        </>
    );
};

export default App;
